	module.exports = {"data":{"markdownRemark":{"html":"<p>博士生课，请L君来讲美国的市场经济与政府的关系，一个与我们现在发展有密切关联的题目。</p>\n<p>基本论点是如何在市场经济和公共政治这两个在基本逻辑上有着某种冲突的领域之间找出协调的机制。这样，社会才能健康发展，同时市场经济也有可能成为稳定的经济。从一般逻辑上说，市场经济以个人追求利益的激励为动力机制，而政治要以保障公共利益为动力机制。这两种机制的逻辑之间如何来平衡，是发展市场经济时须加以解决的难题。基本的做法是：在宪法上要有制度的结构，在政府设置上要有合适的体制，在事务操作上要有明确的程序，在总体机制上要有合适的反馈和保障体制。这些容易归纳，但真正要做到就不容易了。我们在市场经济的发展过程中，如何做出体制安排是关键的环节。</p>\n<p>中西之间的文化差异很大，在资本主义市场经济体制下可以运作的机制，到中国社会和文化中来，可能不行。因此，如何发展出一套中国社会特质和文化的市场经济体制，不仅是对中国社会发展的重要贡献，也是对整个人类社会发展的重要贡献。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0185.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0185.md","frontmatter":{"title":"11月14日 星期一"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0185.md absPath of file >>> MarkdownRemark"}}

